import Vue from 'vue'
import VueRouter from 'vue-router'
import Bio from '@/views/Bio.vue'
import Portfolio from '@/views/Portfolio.vue'
import CurriculumVitae from '@/views/CurriculumVitae.vue'
import Message from '@/views/Message.vue'
import Contact from '@/views/Contact.vue'
import defaultLayout from '@/layouts/Main.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Bio',
    components: {
      content: Bio,
      layout: defaultLayout
    }
  },
  {
    path: '/portfolio',
    name: 'Portfolio',
    components: {
      content: Portfolio,
      layout: defaultLayout
    }
  },
  {
    path: '/cv',
    name: 'CV',
    components: {
      content: CurriculumVitae,
      layout: defaultLayout
    }
  },
  {
    path: '/message',
    name: 'Message',
    components: {
      content: Message,
      layout: defaultLayout
    }
  },
  {
    path: '/contact',
    name: 'Contact',
    components: {
      content: Contact,
      layout: defaultLayout
    }
  },
  {
    path: '*',
    name: 'Bio',
    components: {
      content: Bio,
      layout: defaultLayout
    }
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
