import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './index.css'
import './animate.css'
import './style.css'

import LoadScript from 'vue-plugin-load-script'
Vue.use(LoadScript)

window.isAnimeLoaded = false
Vue.loadScript("https://juliangarnier.com/js/anime.js")
.then(() => {
  window.isAnimeLoaded = true
})
.catch(() => {})

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
