<template>
  <div class="h-16 w-full flex-shrink-0 md:rounded-b-3xl flex bg-white">
    <div class="flex w-full">
      <div
        v-for="(item, index) in buttons"
        :key="index"
        @click="goTo(item.url)" 
        class="flex-1 h-full flex justify-center hover:bg-customgray  cursor-pointer items-center flex justify-center select-none"
        :class="index === 0 ? 'rounded-bl-3xl' : index === (buttons.length - 1) ? 'rounded-br-3xl' : ''">
        <img :src="item.icon" />
      </div>
    </div>
  </div>
</template>

<script>
import common from '@/mixins/common'

export default {
  mixins: [common],
  data() {
    return {
      buttons: [
        {
          icon: 'https://img.icons8.com/windows/1x/hand-lizard.png',
          url: '/bio'
        },
        {
          icon: 'https://img.icons8.com/windows/1x/metal-music.png',
          url: '/cv'
        },
        {
          icon: 'https://img.icons8.com/windows/1x/take-it-easy.png',
          url: '/contact'
        }
      ]
    }
  }
}
</script>