<template>
  <div class="w-full h-full overflow-hidden">
    <div>
      <div class="messages" />
    </div>
  </div>
</template>

<script>
import '@/utils/introduction.js'

export default {
  mounted() {
      this.doIntroduction()
  },
  methods: {
    doIntroduction() {
      if (window.isAnimeLoaded) {
        if (window.isIntroductionRendered) {
          window.doIntro(false)
        } else {
          window.doIntro()
        }
      } else {
        setTimeout(() => {
          this.doIntroduction()
        }, 1000);
      }
    }
  }
}
</script>

